<template>
  <div id="app">
    <router-view v-wechat-title="$route.meta.title"></router-view>
  </div>
</template>

<script>
  export default {
  name: 'app',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    mounted:function () {
      this.http('get','/index/home',{
        pagesize:20,
        page:1
      },function (data) {
      })
    },
}
</script>

<style lang="scss" >
@import "./style/common";
  #app {
    width: 7.5rem;
    height: 100vh;
    margin: 0 auto;
    position: relative;
  }
.my-toast {
  background-color: #fff;
  padding:.16rem .4rem!important;
  span {
    color: #fff;
  }
}
</style>



